.maalcie-tabel .groep-tabs li span.fa {
  color: #a9a9a9;
}
.maalcie-tabel .groep-tabs li:hover span.fa {
  color: #FFFFFF;
}
.rowColor0 {
  background-color: #EBEBEB;
}
.rowColor1 {
  background-color: #FAFAFA;
}

/* maalcie-menu */

#maalcie-menu {
  width: 100%;
}
#maalcie-menu td {
  height: 20px;
}
#maalcie-menu td.active {
  font-weight: bold;
}
.maalcie-beheer-menu div.active {
  font-weight: bold;
}

/* maalcie-tabel */

.maalcie-tabel {
  width: 100%;
  border-spacing: 0;
}
.maalcie-tabel tr {
  background-color: #EBEBEB;
}
.maalcie-tabel tr:nth-child(even) {
  background-color: #FAFAFA;
}
.maalcie-tabel tr:hover {
  background-color: #DDEEFF;
}
.maalcie-tabel th {
  background-color: #FAFAFA;
  font-weight: bold;
  padding: 1em;
  border-bottom: 2px solid #FFFFFF;
}
.maalcie-tabel td {
  vertical-align: middle;
  padding: 0 1em;
  white-space: nowrap;
}
.maalcie-tabel .col-del {
  text-align: center;
}
tr.taak-datum-head th, tr.taak-datum-summary th {
  cursor: pointer;
  border-top: 5px solid #FFFFFF;
}
#taak-datum-head-first th {
  cursor: default;
}
#suggesties-tabel {
  padding: 0;
}
#suggesties-tabel tr:hover, tr.taak-datum-summary:hover th {
  background-color: #DDEEFF !important;
}
#suggesties-controls {
  width: 100%;
  background-color: #EEEEEE;
  border: 1px solid #DDDDDD;
}
#suggesties-controls td {
  padding: .5em;
}
#suggesties-controls label {
  margin-right: 1em;
}
.maalcie-rooster tr {
  background-color: transparent !important;
}
.maalcie-rooster td {
  width: 5px;
}
.maalcie-rooster td.taak {
  width: 170px;
}

/* cell style */

.maaltijd-open, .maaltijd-aangemeld, .abonnement-ingeschakeld, .voorkeur-ingeschakeld, .functie-toegewezen, .taak-self { /* .taak-toegewezen */
  background-color: #AAEEAA;
  font-weight: bold;
  color: green;
  text-align: center;
}
.maaltijd-gesloten, .maaltijd-afgemeld, .abonnement-uitgeschakeld, .voorkeur-uitgeschakeld, .functie-open { /* .taak-open */
  background-color: #EEAAAA;
  font-weight: bold;
  color: red;
  text-align: center;
}

.taak-self {
  text-align: left;
}
td.maaltijd-aangemeld, td.maaltijd-afgemeld {
  text-align: left;
  padding-left: 20px;
}
.abonnement-warning, .taak-warning {
  background-color: #FFEE66;
}
.abonnement-error {
  background-color: #000000;
}
.abonnement-error, .abonnement-warning, .maaltijd-gasten {
  text-align: center;
}
.taak-grijs, .taak-grijs a {
  color: grey;
}
.taak-oud {
  opacity: .50;
}
.taak-maaltijd-oud, .taak-datum-oud {
  display: none;
}

.maaltijd-id {
  color: #999;
}
